<template>
    <views class="shop">
        <nav-bar
            title="附近门店"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <views class="shop-container">
            <shop-item
                v-for="(item, index) in shopList"
                :key="index"
                :item="item"
                @click="handleDetail"
                @addr="handleAddr">
            </shop-item>
        </views>
    </views>
</template>

<script>
import ShopItem from '@/components/shopItem'
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    components: {
        NavBar,
        ShopItem
    },
    mixins: [tools],
    props: {
        color: {
            type: String,
            default: '#1989fa'
        }
    },
    data() {
        return {
            shopList: []
        }
    },
    created() {
        this.getShopList()
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        handleDetail(row) {
            this.push('/shop-detail')
        },
        getShopList() {
            api.getShopList().then((res) => {
                this.shopList = res.list
            })
        },
        handleAddr() {
            this.push('/map')
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
